







































import ICON_LEFT_NAV_HEART from '@/assets/left-nav-icon-heart.svg';

export default {
  props: {
    standard: Object,
    label: String,
  },
  data() {
    return {
      ICON_LEFT_NAV_HEART,
    };
  },
  computed: {
    routerLink() {
      return {
        name: 'standard',
        params: {
          id: (this as any).standard.id,
        },
      };
    },
  },
};

